.listBtn {
  height: 100%;
  font-family: Inter;
  font-style: normal;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
  white-space: nowrap;
  -webkit-touch-callout: none;
  /* iOS Safari */
  user-select: none;
  /* Safari */
  user-select: none;
  /* Konqueror HTML */
  user-select: none;
  /* Old versions of Firefox */
  user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                     supported by Chrome, Edge, Opera and Firefox */
  cursor: pointer;
}
.listBtn.w4 {
  font-weight: 400;
}
.listBtn.w5 {
  font-weight: 500;
}
.listBtn.w6 {
