}
.listBtn:hover {
  height: calc(100% - 4px);
  border-bottom: 2px solid #e4e2e2;
  margin-top: 2px;
}
.listBtn:active {
  height: calc(100% - 6px);
  color: #1e2123;
  border-bottom: 3px solid #ffd54f;
  margin-top: 3px;
}
.listBtn:active svg path {
  fill: #1e2123;
}
.listBtn.-selected {
  height: calc(100% - 6px);
  color: #1e2123;
  border-bottom: 3px solid #ffd54f;
  margin-top: 3px;
}
.listBtn.-selected svg path {
  fill: #1e2123;
}
.listBtn.hidden {
  display: none;
}
.listBtn a {
  margin: 0;
  padding: 0;
