.stdCheckBox {
  position: relative;
  display: flex;
  gap: 10px;
  cursor: pointer;
  color: #1e2123;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  font-style: normal;
  font-family: "Inter";
}

.customCheckbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.customCheckbox + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
}
.customCheckbox + label::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  flex-grow: 0;
  background: #F8F8F8;
  border: 1px solid #F0F0F0;
  border-radius: 5px;
  background-repeat: no-repeat;
  background-position: center center;
  margin-right: 10px;
}
.customCheckbox:checked + label::before {
  border-color: #664EF9;
  background-color: #664EF9;
  background-image: url(../images/checkImg.svg);
}
.customCheckbox:not(:disabled):not(:checked) + label:hover::before {
  background: rgba(102, 78, 249, 0.1);
  border: 1px solid rgba(102, 78, 249, 0.1);
  background-image: url(../images/checkImg.svg);
  background-repeat: no-repeat;
  background-position: center center;
}
.customCheckbox:not(:disabled):active + label::before {
  border-color: #664EF9;
  background-color: #664EF9;
}
.customCheckbox:disabled + label::before {
  background: rgba(102, 78, 249, 0.1);
  border: 1px solid rgba(102, 78, 249, 0.1);
  background-image: url(../images/checkImg.svg);
}

.toggleBtn {
  display: flex;
  gap: 8px;
  cursor: pointer;
}
.toggleBtn label.toggle {
  width: 34px;
  height: 20px;
  border-radius: 100px;
  display: inline-block;
  justify-content: center;
  align-self: center;
  background: #BDBDBD;
  position: relative;
}
.toggleBtn label input {
  display: none;
}
.toggleBtn label span.slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  transition: 0.2s;
  border-radius: 100px;
}
.toggleBtn label span.slider:before {
  position: absolute;
  content: "";
  margin: 3px;
  width: 14px;
  height: 14px;
  background: #FFFFFF;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
  transition: 0.2s;
}
.toggleBtn label input:checked + .slider:before {
  transform: translateX(15px);
}
.toggleBtn label input:checked + .slider {
  background-color: #664EF9;
}
.toggleBtn_title {
  color: #686868;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-style: normal;
  font-family: "Inter";
}

